*::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

*::-webkit-scrollbar-thumb {
  background: rgb(255, 255, 255);
  border-radius: 4px;
}
*::-webkit-scrollbar-thumb:hover {
  background: rgb(213, 213, 213);
  box-shadow: 0 0 2px 1px rgb(0, 0, 0);
}
*::-webkit-scrollbar-thumb:active {
  background-color: rgb(255, 255, 255);
}

*::-webkit-scrollbar-track {
  background: rgb(41, 41, 41);
  border-radius: 4px;
}
*::-webkit-scrollbar-track:hover {
  background: rgb(124, 124, 124);
}
*::-webkit-scrollbar-track:active {
  background: rgb(213, 213, 213);
}

.alerta {
  padding: 1rem 2rem;
  text-align: center;
  display: block;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  background-color: rgb(247, 44, 44);
}
.alerta.exito {
  background-color: rgb(23, 200, 97);
}

.menu {
  width: 4rem;
  height: 4rem;
  padding: 0.5rem;
  background-color: rgb(124, 124, 124);
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: 0.3s all ease;
}
.menu:hover {
  cursor: pointer;
  background-color: #636363;
}
.menu:hover::before {
  background-color: #e6e6e6;
  box-shadow: 0px 1.25rem 0px 0px #e6e6e6;
}
.menu:hover::after {
  background-color: #e6e6e6;
}
.menu::before {
  content: "";
  display: block;
  transition: 0.6s all ease;
  width: 100%;
  height: 0.5rem;
  border-radius: 0.25rem;
  margin: 0 auto;
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 1.25rem 0px 0px rgb(255, 255, 255);
  transform: rotate(0) translateX(0) translateY(0);
}
.menu::after {
  content: "";
  display: block;
  transition: 0.6s all ease;
  width: 100%;
  height: 0.5rem;
  border-radius: 0.25rem;
  margin: 0 auto;
  background-color: rgb(255, 255, 255);
  transform: rotate(0) translateX(0) translateY(0);
}
.menu.menu-activo::before {
  transform: rotate(-45deg) translateX(-0.75rem) translateY(0.75rem);
  box-shadow: none;
}
.menu.menu-activo::before:hover {
  box-shadow: none;
}
.menu.menu-activo::after {
  transform: rotate(45deg) translateX(-1rem) translateY(-1rem);
}

.tres-puntos {
  width: 1.5rem;
  height: 4rem;
  padding: 0.5rem;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: 0.3s all ease;
}
.tres-puntos:hover {
  cursor: pointer;
  background-color: #636363;
}
.tres-puntos:hover::before {
  background-color: #e6e6e6;
  box-shadow: 0px 1.25rem 0px 0px #e6e6e6;
}
.tres-puntos:hover::after {
  background-color: #e6e6e6;
}
.tres-puntos::before {
  content: "";
  display: block;
  transition: 0.6s all ease;
  width: 100%;
  height: 0.5rem;
  border-radius: 0.25rem;
  margin: 0 auto;
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 1.25rem 0px 0px rgb(255, 255, 255);
  transform: rotate(0) translateX(0) translateY(0);
}
.tres-puntos::after {
  content: "";
  display: block;
  transition: 0.6s all ease;
  width: 100%;
  height: 0.5rem;
  border-radius: 0.25rem;
  margin: 0 auto;
  background-color: rgb(255, 255, 255);
  transform: rotate(0) translateX(0) translateY(0);
}

.contenido {
  margin-top: 14rem;
}
@media (min-width: 768px) {
  .contenido {
    margin-top: 0;
    display: flex;
    height: 100vh;
  }
  .contenido .sidebar {
    flex: 0 0 30rem;
  }
}
@media (min-width: 768px) and (min-width: 992px) {
  .contenido .sidebar {
    flex: 0 0 40rem;
  }
}
@media (min-width: 768px) {
  .contenido .contenido-principal {
    flex: 1;
    overflow: auto;
  }
  .contenido .contenido-principal .header {
    left: 30rem;
  }
}
@media (min-width: 768px) and (min-width: 992px) {
  .contenido .contenido-principal .header {
    left: 40rem;
  }
}
.contenido .contenido-principal {
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .contenido .contenido-principal {
    padding-top: 0;
  }
}
.contenido .encabezado {
  min-height: 0;
}
@media (min-width: 768px) {
  .contenido .encabezado {
    min-height: 10rem;
  }
}

.sidebar {
  padding: 3rem 2rem;
}
.sidebar .autor {
  display: grid;
  grid-template-columns: 1fr 2fr;
  column-gap: 2rem;
  background-color: rgb(39, 89, 215);
  padding: 1rem;
  border-radius: 0.5rem;
}
.sidebar .autor img {
  grid-row: 1/3;
}
.sidebar .autor .nombre,
.sidebar .autor .usuario {
  grid-column: 2/3;
  display: grid;
  place-content: center;
}
.sidebar .favorito {
  margin: 4rem 2rem;
  width: 2rem;
  height: 2rem;
  transform: rotate(45deg);
  position: relative;
  cursor: pointer;
}
.sidebar .favorito::before, .sidebar .favorito::after {
  content: "";
  position: absolute;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
}
.sidebar .favorito::before {
  left: -1rem;
  top: 0;
}
.sidebar .favorito::after {
  top: -1rem;
}
.sidebar .favorito.activo {
  background-color: rgb(247, 44, 44);
}
.sidebar .favorito.activo::after, .sidebar .favorito.activo::before {
  background-color: rgb(247, 44, 44);
}
.sidebar .favorito.activo:hover {
  background-color: #e70909;
}
.sidebar .favorito.activo:hover::after, .sidebar .favorito.activo:hover::before {
  background-color: #e70909;
}
.sidebar .favorito.inactivo {
  background-color: rgb(213, 213, 213);
}
.sidebar .favorito.inactivo::after, .sidebar .favorito.inactivo::before {
  background-color: rgb(213, 213, 213);
}
.sidebar .favorito.inactivo:hover {
  background-color: #bcbcbc;
}
.sidebar .favorito.inactivo:hover::after, .sidebar .favorito.inactivo:hover::before {
  background-color: #bcbcbc;
}
.sidebar .opciones {
  display: grid;
  gap: 2rem;
  margin-top: 3rem;
}
.sidebar .opciones button {
  padding: 1rem;
  font-size: 1.3rem;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 3rem 0 2rem 0;
  background: linear-gradient(to top, transparent, rgb(41, 41, 41));
}
.header .contenedor {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .navegacion {
  opacity: 0;
  visibility: hidden;
  width: 0;
  height: 0;
}
@media (min-width: 992px) {
  .header .navegacion {
    opacity: 1;
    visibility: visible;
    width: auto;
    height: auto;
  }
}
@media (min-width: 992px) {
  .header .menu {
    display: none;
  }
}
@media (max-width: 992px) {
  .header.header-fijo {
    z-index: 10;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.4);
  }
  .header.header-fijo .logo {
    display: none;
  }
  .header.header-fijo .menu {
    position: absolute;
